            <div
              class="w-100 step__input"
              v-if="
                form.maitredouvrage === '05_Socit Commerciale' ||
                  form.maitredouvrage === '07_Collectivit Locale'
              "
            >
              <!-- Field 2 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Denomination</label
                  >
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.denomination"
                    type="text"
                    required
                  ></b-form-input>
                </div>
              </div>
              <!-- Field 2 end -->
            </div>
            <div
              class="w-100 step__input"
              v-if="
                form.maitredouvrage === '' ||
                  (form.maitredouvrage !== '05_Socit Commerciale' &&
                    form.maitredouvrage !== '07_Collectivit Locale')
              "
            >
              <!-- Field 2 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1">Nom</label>
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.nom"
                    type="text"
                    required
                  ></b-form-input>
                </div>
              </div>
              <!-- Field 2 end -->
            </div>
            <div
              class="w-100 step__input"
              v-if="
                form.maitredouvrage === '' ||
                  (form.maitredouvrage !== '05_Socit Commerciale' &&
                    form.maitredouvrage !== '07_Collectivit Locale')
              "
            >
              <!-- Field 2 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >CIN/Passeport</label
                  >
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.cinPasseport"
                    type="text"
                    required
                  ></b-form-input>
                </div>
              </div>
              <!-- Field 2 end -->
            </div>
            <div
              class="w-100 step__input"
              v-if="
                form.maitredouvrage === '' ||
                  (form.maitredouvrage !== '05_Socit Commerciale' &&
                    form.maitredouvrage !== '07_Collectivit Locale')
              "
            >
              <!-- Field 2 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1">Prenom</label>
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.prenom"
                    type="text"
