                  ></b-form-input>
                </div>
              </div>
              <!-- Field 2 end -->
            </div>
            <div
              class="w-100 step__input"
              v-if="form.maitredouvrage === '05_Socit Commerciale'"
            >
              <!-- Field 3 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Registre numero</label
                  >
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
