                    required
                  ></b-form-input>
                </div>
              </div>
              <!-- Field 4 end -->
            </div>
            <div class="w-100 step__input">
              <!-- Field 5 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
