                    :disabled="processing"
                    required
                  ></b-form-select>
                </div>
              </div>
              <!-- Field 6 end -->
            </div>
            <div class="w-100 step__input">
              <!-- Field 7 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
