        </div>
      </div>
      <div class="col-12">
        <div class="home__stage step__block mt-3">
          <div class="step__block__header">
            <div class="step__block__header__content">
              Matre douvrage : Informations
            </div>
          </div>
          <table class="text-center table table-bordered">
            <thead>
              <tr style="background-color: #d58512;color: white;">
                <th class="text-center">N</th>
                <th class="text-center">Initiale</th>
                <th class="text-center">Identifiant</th>
                <th class="text-center">Dnomination</th>
                <th class="text-center">Adresse</th>
                <th class="text-center">Prfecture/Commune</th>
                <th class="text-center">Supprimer</th>
              </tr>
            </thead>
            <tbody>
              <template v-for="(owner, index) in owners">
                <tr :key="index">
                  <td class="text-center">{{ index + 1 }}</td>
                  <td class="text-center">{{ owner.id }}</td>
                  <td class="text-center">{{ owner.Identifiant }}</td>
                  <td class="text-center">{{ owner.denomination }}</td>
                  <td class="text-center">{{ owner.address }}</td>
                  <td class="text-center">
                    {{ owner.provinceText }} {{ owner.communeText }}
                  </td>
                  <td class="text-center">
                    <button class="button button--green" @click="remove(index)">
                      Supprimer
                    </button>
                  </td>
                </tr>
              </template>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div class="row">
      <div v-if="error !== ''" class="col-12">
        <div class="text-danger">{{ error }}</div>
      </div>
    </div>
    <div class="row mt-4">
      <div class="col-6 text-left">
        <a
          href="javascript:void(0)"
          @click="prevStep"
          class="button button--blue"
          >Prcdent</a
        >
      </div>
      <div class="col-6 text-right">
        <a
          href="javascript:void(0)"
          @click="nextStep"
          class="button button--blue"
          >suivant</a
        >
      </div>
    </div>
  </div>
</template>
<script>
import { GET_COMMUNE } from "@/core/store/app.module";
import { mapState } from "vuex";
export default {
  name: "Step-2",
  data: () => ({
    owners: [],
    processing: false,
    error: "",
    form: {
      maitredouvrage: "",
      cinPasseport: "",
      nom: "",
      prenom: "",
      registreNumbero: "",
      denomination: "",
      adresse: "",
      prefectureOuProvince: "",
      commune: "",
      codePostale: "",
      nationalite: ""
    }
  }),
  methods: {
    remove(index) {
      this.owners = this.owners.filter(
        (owner, indx) => owner && index !== indx
      );
    },
    nextStep() {
      if (this.owners.length < 1) {
        this.error = "Please add an owner first.";
        setTimeout(() => {
          this.error = "";
        }, 2000);
        return;
      }
      this.$emit("go-next",this.owners);
    },
    prevStep() {
      this.$emit("go-back");
    },
    async changeProvince(id) {
      this.processing = true;
      this.form.commune = "";
      await this.$store.dispatch(GET_COMMUNE, id);
      this.processing = false;
    },
    submitForm() {
      const temp = {
        maitredouvrage: "",
        cinPasseport: "",
        nom: "",
        prenom: "",
        registreNumbero: "",
        denomination: "",
        adresse: "",
        prefectureOuProvince: "",
        commune: "",
        codePostale: "",
        nationalite: ""
      };
      const { form } = this;
      const owner = {
        id: form.maitredouvrage,
        firstname: form.nom,
        lastname: form.prenom,
        denomination:
          form.denomination === ""
            ? `${form.nom} ${form.prenom}`
            : form.denomination,
        passport: form.cinPasseport,
        address: form.adresse,
        province: form.prefectureOuProvince,
        commune: form.commune,
        provinceText: this.prefectureOuProvinceOptions.find(
          x => x.value === form.prefectureOuProvince
        ).text,
        communeText: this.communeOptions.find(x => x.value === form.commune)
          .text,
        // arrond: form.maitredouvrage,
        postalcode: form.codePostale,
        Identifiant: form.registreNumbero || form.cinPasseport,
        country: form.nationalite
      };
      console.log("submitForm | owner", owner);
      this.owners.push(owner);
      this.form = temp;
    }
  },
  computed: {
    ...mapState({
      maitreDouvrageOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.maitreDouvrage
      ],
      communeOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.commune
      ],
      prefectureOuProvinceOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.prefectureOuProvince
      ],
      countries: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.countries
      ]
    })
  }
};
</script>
