<template>
  <div class="fourth-step step">
    <div class="row">
      <div class="col-12">
        <p style="color:red;">
          TAILLE MAXIMALE PAR FICHIER = 15MO, Evitez le tlchargement des
          fichiers lourds (images hautes rsolution, couleurs...) Utilisez, le
          cas chant, le site
          <a href="www.ilovepdf.com" target="_blank">www.ilovepdf.com</a> pour
          optimiser les fichiers PDF
        </p>
      </div>
      <div class="col-6">
        <vue-dropzone
          ref="myVueDropzone"
          :useCustomSlot="true"
          id="dropzone"
          @vdropzone-upload-progress="e => uploadProgress(e, 'contract_file')"
          :options="dropzoneOptions1"
          @vdropzone-file-added="fileAdded1"
          @vdropzone-sending-multiple="e => sendingFiles(e, 'contract_file')"
          @vdropzone-success="success1"
          @vdropzone-removed-file="removeFile1"
          @vdropzone-queue-complete="queueComplete1"
        >
          <div class="dropzone-custom-content">
            Glisser ou double click pour charger les fichiers de plan +
          </div>
        </vue-dropzone>
      </div>
      <div class="col-6">
        <vue-dropzone
          ref="myVueDropzone"
          :useCustomSlot="true"
          id="dropzone"
          @vdropzone-upload-progress="e => uploadProgress(e, 'plan_file')"
          :options="dropzoneOptions2"
          @vdropzone-file-added="fileAdded2"
          @vdropzone-sending-multiple="e => sendingFiles(e, 'plan_file')"
          @vdropzone-success="success2"
          @vdropzone-removed-file="removeFile2"
          @vdropzone-queue-complete="queueComplete2"
        >
          <div class="dropzone-custom-content">
            Glisser ou double click pour charger le fichier de contrat +
          </div></vue-dropzone
        >
      </div>
    </div>
    <div v-if="error !== ''" class="col-12">
      <div class="text-danger">{{ error }}</div>
    </div>
    <div class="col-12 text-right mt-4">
      <a href="javascript:void(0)" @click="nextStep" class="button button--blue"
        >suivant</a
      >
    </div>
  </div>
</template>
<script>
import vue2Dropzone from "vue2-dropzone";
import "vue2-dropzone/dist/vue2Dropzone.min.css";
export default {
  data: () => ({
    error: "",
    files: {
      left: [],
      right: []
    },
    leftFilesUploading: [],
    rightFilesUploading: [],
    dropzoneOptions1: {
      url: "https://dev.croatanger.org/api/v1/upload",
      maxFilesize: 42,
      dictDefaultMessage:
        "Glisser ou double click pour charger les fichiers de plan +",
      thumbnailWidth: 140,
      thumbnailHeight: 140,
      uploadMultiple: false,
      addRemoveLinks: true,
      paramName: "plan_files"
    },
    dropzoneOptions2: {
      url: "https://dev.croatanger.org/api/v1/upload",
      maxFilesize: 42,
      dictDefaultMessage:
        "Glisser ou double click pour charger le fichier de contrat +",
      thumbnailWidth: 140,
      thumbnailHeight: 140,
      uploadMultiple: false,
      addRemoveLinks: true,
      paramName: "contract_files"
    }
  }),
  components: {
    vueDropzone: vue2Dropzone
  },
  name: "Step-1",
  methods: {
    paramName() {
      return "file[]";
    },
    uploadProgress() {},
    fileAdded1(file) {
      this.leftFilesUploading.push(file);
      this.$emit("file-uploading", {
        side: "left"
      });
    },
    fileAdded2(file) {
      this.rightFilesUploading.push(file);
      this.$emit("file-uploading", {
        side: "right"
      });
    },
    success1(file, res) {
      const len = this.filesUploading.length;
      this.filesUploading = this.filesUploading.splice(0, len - 2);
      const x = "plan_file";
      this.files.left.push(res.success);
      let obj = {};
      obj[x] = res.success;
      obj.side = "left";
      this.$emit("file-uploaded", obj);
    },
    success2(file, res) {
      const x = "contract_file";
      this.files.right.push(res.success);
      let obj = {};
      obj[x] = res.success;
      obj.side = "right";
      this.$emit("file-uploaded", obj);
    },
    removeFile1(file) {
      if (this.leftFilesUploading.length > 0) {
        this.$emit("file-stopped-uploading", {
          side: "left"
        });
      }
      this.leftFilesUploading = this.leftFilesUploading.filter(f => {
        f.name !== file.name;
      });
    },
    removeFile2(file) {
      if (this.rightFilesUploading.length > 0) {
        this.$emit("file-stopped-uploading", {
          side: "right"
        });
      }
      this.rightFilesUploading = this.rightFilesUploading.filter(f => {
        f.name !== file.name;
      });
    },
    queueComplete1() {
      this.leftFilesUploading = [];
      this.$emit("queue-done", {
        side: "left"
      });
    },
    queueComplete2() {
      this.rightFilesUploading = [];
      this.$emit("queue-done", {
        side: "right"
      });
    },
    nextStep() {
      this.$emit("go-next");
    }
  }
};
</script>
<style lang="scss" scoped></style>
