<script>
import {
  GET_COMMUNE,
  GET_DECLARATIONS,
  GET_DECLARATION_TYPES,
  GET_ARROND
} from "@/core/store/app.module";
import { mapState } from "vuex";
export default {
  name: "Step-2",
  data: () => ({
    error: "",
    projects: [],
    form: {
      categorieDeProjet: "",
      leTypeDeDeclaration: "",
      declaration: "",
      surfacePlancherCouverts: "",
      descriptionProjet: "",
      attestation: "",
      prefectureOuProvince: "",
      commune: "",
      arrondissement: "",
      adresseDuProjet: "",
      centroideLambertDuProjetX: "",
      centroideLambertDuProjetY: ""
    },
    processing: false
  }),
  methods: {
    nextStep() {
      this.$refs.submitFormRef.$el.submit();
    },
    prevStep() {
      this.$emit("go-back");
    },
    submitForm() {
      const { form } = this;

      if (this.projects.length < 1) {
        this.error = "Please add an project first.";
        setTimeout(() => {
          this.error = "";
        }, 2000);
      }
      const project = {
        province: this.form.prefectureOuProvince,
        commune: this.form.commune,
        arrond: this.form.arrondissement,
        address: this.form.adresseDuProjet,

        cordonne_x: form.centroideLambertDuProjetX,
        cordonne_y: form.centroideLambertDuProjetY
      };
      project.project = this.projects;
      this.$emit("go-next", project);
    },
    async changeProvince(id) {
      this.processing = true;
      this.form.categorieDeProjet = "";
      await this.$store.dispatch(GET_COMMUNE, id);
      this.processing = false;
    },
    async changeCommune(id) {
      this.processing = true;
      this.form.arrondissement = "";
      await this.$store.dispatch(GET_ARROND, id);
      this.processing = false;
    },
    removeProject(idx) {
      this.proects = this.proects.filter(
        (project, indx) => project && idx !== indx
      );
    },
    addProject() {
      const { form } = this;
      const temp = {
        categorieDeProjet: "",
        leTypeDeDeclaration: "",
        declaration: "",
        surfacePlancherCouverts: "",
        descriptionProjet: "",
        attestation: ""
      };
      const project = {
        category: form.categorieDeProjet,
        catName: this.categorieDeProjetOptions.find(
          x => x.value === form.categorieDeProjet
        ).text,
        type: form.leTypeDeDeclaration,
        typeName: this.leTypeDeDeclarationOptions.find(
          x => x.value === form.leTypeDeDeclaration
        ).text,
        declaration: form.declaration,
        surface: form.surfacePlancherCouverts,
        description: form.descriptionProjet,
        attestation: form.attestation
      };
      Object.keys(temp).forEach(key => {
        this.form[key] = "";
      });
      this.projects.push(project);
    },
    async ChangeCat(id) {
      this.processing = true;
      this.form.leTypeDeDeclaration = "";
      this.form.declaration = "";
      await this.$store.dispatch(GET_DECLARATIONS, id);
      await this.$store.dispatch(GET_DECLARATION_TYPES, id);
      this.processing = false;
    }
  },
  computed: {
    ...mapState({
      categorieDeProjetOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.projectCats
      ],
      communeOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.commune
      ],
      prefectureOuProvinceOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.prefectureOuProvince
      ],
      leTypeDeDeclarationOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.declarationTypes
      ],
      declarationOptions: state => [
        { value: "", text: "Veuillez selectionnez" },
        ...state.app.selectOptions.declarations
      ],
      arrondissementOptions: state => [
        {
          value: "",
          text: "Slectionnez Arrondissement"
        },
        ...state.app.selectOptions.arronds
      ]
    })
  }
};
</script>
