<template>
  <div class="first-step step">
    <div class="row">
      <div class="col-12">
        <div class="home__stage mt-3 step__block">
          <div class="step__block__header">
            <div class="step__block__header__content">
              Les Rgimes fonciers
            </div>
          </div>
          <form @submit.prevent="submitForm">
            <div class="w-100 step__input">
              <!-- Field 1 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Rgime foncier
                  </label>
                </div>
                <div class="col-7">
                  <b-form-select
                    class="step__input__select"
                    v-model="form.regimeFoncier"
                    :options="regimeFoncierOptions"
                    required
                  ></b-form-select>
                </div>
              </div>
              <!-- Field 1 end -->
            </div>
            <div class="w-100 step__input" v-if="form.regimeFoncier === 'IF'">
              <!-- Field 1 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >&nbsp;
                  </label>
                </div>
                <div class="col-7">
                  <b-form-select
                    class="step__input__select"
                    v-model="form.regime2"
                    :options="regime2Options"
                    required
                  ></b-form-select>
                </div>
              </div>
              <!-- Field 1 end -->
            </div>
            <div class="w-100 step__input">
              <!-- Field 5 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Statut foncier</label
                  >
                </div>
                <div class="col-7">
                  <b-form-select
                    class="step__input__select"
                    v-model="form.statutFoncier"
                    :options="statutFoncierOptions"
                  ></b-form-select>
                </div>
              </div>
              <!-- Field 5 end -->
            </div>
            <div class="w-100 step__input" v-if="form.regimeFoncier === 'IF'">
              <!-- Field 5 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1">indice</label>
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.indice"
                    type="number"
                    placeholder="Indice"
                    required
                  />
                </div>
              </div>
              <!-- Field 5 end -->
            </div>
            <div class="w-100 step__input" v-if="form.regimeFoncier === 'IF'">
              <!-- Field 5 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Numero Order</label
                  >
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.numeroOrder"
                    type="number"
                    placeholder="Numero Order"
                    required
                  />
                </div>
              </div>
              <!-- Field 5 end -->
            </div>
            <div class="w-100 step__input">
              <!-- Field 5 Start -->
              <div class="row">
                <div class="col-5">
                  <label class="step__input__label" for="input-1"
                    >Rfrence du supportFoncier</label
                  >
                </div>
                <div class="col-7">
                  <b-form-input
                    class="step__input__text"
                    v-model="form.supportFoncier"
                    type="text"
                    placeholder="Rfrence du support Foncier"
                  />
                </div>
              </div>
              <!-- Field 5 end -->
            </div>
            <div class="w-100 text-right">
              <button type="submit" class="button button--green">
                Enregistrer et ajouter un autre Matre d'ouvrage
              </button>
            </div>
          </form>
        </div>
      </div>
      <div class="col-12">
        <div class="home__stage step__block mt-3">
          <div class="step__block__header">
            <div class="step__block__header__content">
              Matre douvrage : Informations
            </div>
          </div>
          <table class="text-center table table-bordered">
            <thead>
              <tr style="background-color: #d58512;color: white;">
                <th class="text-center">N</th>
                <th class="text-center">Rgime foncier</th>
                <th class="text-center">Support foncier</th>
                <th class="text-center">Statut foncier</th>
                <th class="text-center" style="width:10%">Supprimer</th>
              </tr>
            </thead>
            <tbody>
              <template v-for="(project, index) in lands">
                <tr :key="index">
                  <td class="text-center">{{ index + 1 }}</td>
                  <td class="text-center">{{ project.regime }}</td>
                  <td class="text-center">{{ project.reference }}</td>
                  <td class="text-center">{{ project.status }}</td>
                  <td class="text-center">
                    <button class="button button--green" @click="remove(index)">
                      Supprimer
                    </button>
                  </td>
                </tr>
              </template>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <div class="row mt-4">
      <div class="col-6 text-left">
        <a
          href="javascript:void(0)"
          @click="prevStep"
          class="button button--blue"
          >Prcdent</a
        >
      </div>
      <div class="col-6 text-right">
        <a
          href="javascript:void(0)"
          @click="nextStep"
          class="button button--green"
          >Valider</a
        >
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "Step-4",
  props: {
    formData: Object
  },
  data: () => ({
    lands: [],
    form: {
      regime2: "",
      numeroOrder: "",
      indice: "",
      regimeFoncier: "",
      statutFoncier: "",
      supportFoncier: ""
    },
    regime2Options: [
      { value: "", text: "Veuillez slectionnez le rgime foncier" },
      { value: "Titre Foncier", text: "Titre Foncier" },
      {
        value: "Rquisition d'immatriculation",
        text: " Rquisition d'immatriculation"
      }
    ]
  }),
  methods: {
    remove(index) {
      this.lands = this.lands.filter((land, indx) => land && index !== indx);
    },
    nextStep() {
      this.$emit("go-next", this.lands);
      this.$emit("can-submit");
    },
    prevStep() {
      this.$emit("go-back");
    },
    submitForm() {
      const temp = {
        numeroOrder: "",
        indice: "",
        regimeFoncier: "",
        statutFoncier: "",
        supportFoncier: ""
      };
      const land = {
        regime: this.formData.projects.province,
        regime2:
          this.form.regimeFoncier === "IF"
            ? this.formData.projects.commune
            : this.form.regime2,
        reference: this.form.supportFoncier,
        order: this.form.numeroOrder,
        index: this.form.indice,
        status: this.form.statutFoncier
      };
      this.lands.push(land);
      this.form = temp;
    }
  },
  computed: {
    ...mapState({
      statutFoncierOptions: state => [
        { value: "", text: "Veuillez Statut foncier" },
        ...state.app.thirdStepOptions.statutFoncier
      ],
      regimeFoncierOptions: state => [
        { value: "", text: "Veuillez slectionnez le rgime foncier" },
        ...state.app.thirdStepOptions.regimeFoncier
      ]
    })
  }
};
</script>
<style lang="scss" scoped></style>
