<template>
  <div class="home">
    <stages :active="step" />
    <div class="row" v-if="submitted">
      <div class="col-12 text-success text-center">Form Submitted</div>
    </div>
    <div
      class="row"
      v-if="uploading.left.length > 0 || uploading.right.length > 0"
    >
      <div class="col-12 text-success text-center">Files Uploading</div>
    </div>
    <div class="row" v-if="error">
      <div class="col-12 text-danger">{{ error }}</div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-12 m-3 home__stage p-4">
          <template v-if="step === 0">
            Loading ...
          </template>
          <template v-else>
            <first-step
              @go-next="goNext"
              v-show="step === 1"
              @file-uploaded="uploaded"
              @file-uploading="fileUploading"
              @file-stopped-uploading="fileStoppedUploading"
              @queue-done="queueDone"
            />
            <second-step
              @go-next="goNextOwner"
              @go-back="goBack"
              e
              v-show="step === 2"
            />
            <third-step
              @go-next="goNextProject"
              @go-back="goBack"
              v-show="step === 3"
            />
            <fourth-step
              :formData="formData"
              @go-next="goNextLand"
              @go-back="goBack"
              v-show="step === 4"
              @can-submit="submitServer"
            />
            <last-step @go-back="goBack" v-show="step === 5" />
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Step1 from "@/components/Step1.vue";
import Step2 from "@/components/Step2.vue";
import Step3 from "@/components/Step3.vue";
import Step4 from "@/components/Step4.vue";
import Step5 from "@/components/Step5.vue";
import Stages from "@/components/Stages.vue";
import {
  GET_PROVINCES,
  /* GET_COUNTRIES */ GET_PROJECT_CATS
} from "@/core/store/app.module";
import axios from "axios";
export default {
  data: () => ({
    step: 0,
    error: "",
    submitted: false,
    formData: {
      documents: {
        plan_files: {
          plan_file: []
        },
        contracts: {
          contract_file: []
        }
      },
      owners: null,
      projects: null,
      lands: null
    },
    uploading: {
      left: [],
      right: []
    }
  }),
  async mounted() {
    await this.$store.dispatch(GET_PROVINCES);
    await this.$store.dispatch(GET_PROJECT_CATS);
    // await this.$store.dispatch(GET_COUNTRIES);
    this.step = 1;
  },
  name: "Home",
  components: {
    "first-step": Step1,
    "second-step": Step2,
    "third-step": Step3,
    "fourth-step": Step4,
    "last-step": Step5,
    stages: Stages
  },
  methods: {
    fileUploading({ side }) {
      this.uploading[side].push("uploading");
    },
    fileStoppedUploading({ side }) {
      this.uploading[side].pop();
    },
    queueDone({ side }) {
      this.uploading[side] = [];
    },
    goNextOwner(owners) {
      this.formData.owners = owners;
      this.goNext();
    },
    goNextProject(projects) {
      this.formData.projects = projects;
      this.goNext();
    },
    uploaded(x) {
      let type = "";
      let url = "";
      Object.entries(x).forEach(([key, value], index) => {
        if (index === 0) {
          type = key;
          url = value;
        }
      });
      if (type === "plan_file") {
        this.formData.documents.plan_files.plan_file.push(url);
      } else if (type === "contract_file") {
        this.formData.documents.contracts.contract_file.push(url);
      }
    },
    goNext() {
      this.step += 1;
    },
    goNextLand(lands) {
      this.formData.lands = lands;
      // this.goNext();
    },
    goBack() {
      this.step -= 1;
    },
    submitServer() {
      if (this.uploading.left.length > 0 || this.uploading.right.length > 0) {
        this.error = "Files are uploading";
        setTimeout(() => {
          this.error = "";
        }, 2000);
        return;
      }
      if (!this.submitted) {
        return new Promise(resolve => {
          axios
            .post("https://dev.croatanger.org/api/v1/ccsubmit", this.formData)
            .then(res => {
              this.submitted = true;
              setTimeout(() => {
                window.location = "https://dev.croatanger.org/";
              }, 100);
              resolve(res);
            });
        });
      }
    }
  }
};
</script>
